<script>
import { UserMixins } from "@/mixins/user";

export default {
  mixins: [UserMixins],
  mounted() {
    this.GET_MESSAGE_API();
  },
  data() {
    return {
      selected: null,
      items: [],
      full_message: null,
      loader: false,
    };
  },
  watch:{
    selected(values){
      this.full_message = null
      this.get_full_message(values)
    }
  },
  methods: {
    get_full_message(message) {
      this.loader = true;
      this.GET_FULLMESSAGE_API("?message_id=" + message.id)
        .then((data) => {
          console.log(data);
          this.full_message = data.data;
          this.loader = false;
        })
        .catch(() => {
          this.full_message = null;
          this.loader = false;
        });
    },
  },
};
</script>

