   

import BackendAxios from "../repo"

const state = {
    Userinfo: {},
    Lables: [],
    getProfile: {},
    message: {},
}

const getters = {
    Userinfo_getters: (state) => state.Userinfo,
    Lables_getters: (state) => state.Lables,
    getProfile_getters: (state) => state.getProfile,
    message_getters: (state) => state.message,


}
const actions = {
    GET_USER_API({ commit }) {
        return new Promise((resolve, reject) => {
            BackendAxios.get('UserAPI/')
                .then(data => {
                    commit('ChangeUserinfo_mutation', data.data)
                    resolve(data)
                })
                .catch(err => {
                    reject(err)
                })
        })
    },
    GET_LABLES_API({ commit }) {
        return new Promise((resolve, reject) => {
            BackendAxios.get('LablesAPI/')
                .then(data => {
                    commit('Lables_mutation', data.data)
                    resolve(data)
                })
                .catch(err => {
                    reject(err)
                })
        })
    },
    GET_getProfile_API({ commit }) {
        commit
        return new Promise((resolve, reject) => {
            BackendAxios.get('api/gmail/getProfile/')
                .then(data => {
                    commit('getProfile_mutation', data.data)
                    resolve(data)
                })
                .catch(err => {
                    reject(err)
                })
        })
    },
    GET_MESSAGE_API({ commit }) {
        commit
        return new Promise((resolve, reject) => {
            BackendAxios.get('api/gmail/message/')
                .then(data => {
                    commit('message_mutation', data.data)
                    resolve(data)
                })
                .catch(err => {
                    reject(err)
                })
        })
    },
    GET_FULLMESSAGE_API({ commit }, query) {
        commit
        return new Promise((resolve, reject) => {
            BackendAxios.get('api/gmail/fullMessage/'+query)
                .then(data => {
                    resolve(data)
                })
                .catch(err => {
                    reject(err)
                })
        })
    },
    
}

const mutations = {
    ChangeUserinfo_mutation(state, data) {
        state.Userinfo = data
    },
    Lables_mutation(state, data) {
        state.Lables = data
    },
    getProfile_mutation(state, data) {
        state.getProfile = data
    },
    message_mutation(state, data) {
        state.message = data
    }

}

export const user__store = {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};